#musicPlayer {
  width:100%;
}

audio::-webkit-media-controls-panel {
  background-color: orange;
  background: none;
  border-radius:0;
}
#playButton {
  width:30px;
  height:50px;
  align-self: center;
  padding: 0px;
}

#playButton:focus {
    outline: none;
}
.songChange:focus {
    outline: none;
}

.songChange {
  width: 30px;
}

#previousSong {
  margin-left:10px;
}

#audioControlPanel {
  display: flex;
  width:100vw;
}

#songTitle {
  color:white;
  margin-right: 20px;
}

#durationView {
  flex-grow: 1;
  height: 12px;;
  background-color: transparent;
  border: .1px solid white;
  align-self: center;
  margin: 10px;
  
}

#durationPosition {
  width:50%;
  height:100%;
  left:0;
  background-color:white;
  bottom:0;
}
